import {
  Message,
  SettingsRequestMessage,
  SettingsResponseMessage,
  SettingsStorage,
} from "@vsintellicode/completions-shared";

/**
 * Remote shim which implements settings storage by asking the extension for
 * the current value of settings, and returning the default if it is not already known.
 */
export class WorkerSettingsStorage implements SettingsStorage {
  private cachedSettings: Map<string, any> = new Map<string, any>();

  constructor(private postMessage: (message: Message) => void) {}
  getExtensionSectionName(): string {
    // This is not intended to be used. Plumbing needs to be finished if it becomes necessary
    return 'workerSettingsPlaceholder';
  }

  public get<T>(key: string, defaultValue: T): T {
    // Try to get the setting from the cache
    const cachedValue = this.cachedSettings.get(this.cacheKey('', key));
    if (cachedValue !== undefined) {
      // We have a cached value, so just return it.
      return cachedValue;
    } else {
      // If it's missing, request the updated value from the main process, and return default
      this.requestUpdate('', key, defaultValue);
      return defaultValue;
    }
  }

  public getNested<T>(innerSection: string, key: string, defaultValue: T): T {
    // Try to get the setting from the cache
    const cachedValue = this.cachedSettings.get(this.cacheKey(innerSection, key));
    if (cachedValue !== undefined) {
      // We have a cached value, so just return it.
      return cachedValue;
    } else {
      // If it's missing, request the updated value from the main process, and return default
      this.requestUpdate(innerSection, key, defaultValue);
      return defaultValue;
    }
  }

  public requestUpdate(section: string, key: string, defaultValue: any) {
    const requestMessage: SettingsRequestMessage = {
      kind: "SettingsRequestMessage",
      section: section,
      key: key,
      default: defaultValue,
    };
    this.postMessage(requestMessage);
  }

  public handleUpdateResponse(response: SettingsResponseMessage) {
    this.cachedSettings.set(
      this.cacheKey(response.section, response.key),
      response.value
    );
  }

  private cacheKey(section: string, key: string): string {
    return `${section}.${key}`;
  }
}
