import {
  ILoggingService,
  LogLevel,
  LogMessage,
  Message,
} from "@vsintellicode/completions-shared";

/**
 * Remote shim which implements ILoggingService by sending messages to the extension
 * telling it what to log.
 */
export class WorkerLoggingService implements ILoggingService {
  constructor(private postMessage: (message: Message) => void) {}
  write(logLevel: LogLevel, str: string): void {
    const logMessage: LogMessage = {
      kind: "LogMessage",
      level: logLevel,
      message: str,
    };
    this.postMessage(logMessage);
  }
}
