import {
  AwaitableMessageExchanger,
  CachingResultMessage,
  ICompletionResultCallback,
  ResponseMessage
} from "@vsintellicode/completions-shared";

/**
 * Remote shim which implements ICompletionResultCallback, sending the inference result
 * back to the extension which may then cache it.
 */
export class WorkerCompletionResultCallback
  implements ICompletionResultCallback
{
  constructor(private exchanger: AwaitableMessageExchanger<ResponseMessage, CachingResultMessage>) {}

  async setLineCompletions(
    requestId: number,
    normContext: string,
    completions: [number | number[], string[]][],
    logConfidenceThreshold: number,
    lengthPenalty: number,
    correlationGuid: string,
  ): Promise<number> {
    let msg: ResponseMessage = {
      kind: "ResponseMessage",
      requestId: requestId,
      completions: completions,
      logConfidenceThreshold: logConfidenceThreshold,
      lengthPenalty: lengthPenalty,
      correlationGuid: correlationGuid,
    };

    const cacheResult = await this.exchanger.exchange(msg);
    return cacheResult.numAccepted;
  }
}
