import {
  ITelemetrySender,
  Message,
  TelemetryMessage,
} from "@vsintellicode/completions-shared";

/**
 * Remote shim which implements ITelemetrySender by sending messages
 * to the extension host which will then send the telemetry.
 */
export class WorkerTelemetryService implements ITelemetrySender {
  constructor(private postMessage: (message: Message) => void) {}
  sendTelemetryEvent(
    eventName: string,
    properties?: { [key: string]: string },
    measures?: { [key: string]: number }
  ): void {
    const telemetryMessage: TelemetryMessage = {
      kind: "TelemetryMessage",
      eventName: eventName,
      properties: properties,
      measures: measures,
    };
    this.postMessage(telemetryMessage);
  }
}
