import {
  AwaitableMessageExchanger,
  CachingResultMessage,
  ICompletionResultCallback,
  ILoggingService,
  LocalLineCompletionService,
  LogLevel,
  Message,
  ResponseMessage,
  Settings,
  TelemetryEventNames,
  TelemetryPropertyNames,
  createLineCompletionServiceWithRetriesAsync,
  OnnxRuntimeLoadFailureError, 
  WorkerErrorCode
} from "@vsintellicode/completions-shared";
import { WorkerCompletionResultCallback } from "./remotedServices/completionResultCallback";
import { WorkerLoggingService } from "./remotedServices/logging";
import { WorkerSettingsStorage } from "./remotedServices/settings";
import { WorkerTelemetryService } from "./remotedServices/telemetry";

/**
 * Persistent state needed in the inference worker process.
 */
class InferenceWorker {
  private responseCacheResultExchanger: AwaitableMessageExchanger<ResponseMessage, CachingResultMessage>;
  private completionState: ICompletionResultCallback;

  /**
   *
   * @param port Port which is used to communicate with the main extension host process
   * @param logger Logging for this worker.
   * @param lineCompletionService Line completion service to run within the worker.
   * @param completionState Completion callback which the line completion service will call when it has predictions.
   * @param settingCache Setting storage which requests settings from the main extension host, and caches the result.
   */
  constructor(
    private logger: ILoggingService,
    private lineCompletionService: LocalLineCompletionService,
    private settingCache: WorkerSettingsStorage
  ) {
    this.responseCacheResultExchanger = new AwaitableMessageExchanger<ResponseMessage, CachingResultMessage>(m => process.send?.(m));
    this.completionState = new WorkerCompletionResultCallback(this.responseCacheResultExchanger);
    process.on("message", (message: any) => {
      this.handleMessage(message);
    });
    process.once("close", () => {
      process?.exit();
    });
  }

  /**
   * Processes incoming messages and takes appropriate actions.
   * @param msg Incoming message to process
   */
  private handleMessage(msg: Message) {
    try {
      switch (msg.kind) {
        case "RequestMessage":
          this.lineCompletionService.request(
            msg.requestId,
            msg.normalizedContext,
            msg.lang,
            this.completionState,
            { isCancellationRequested: false },
            msg.correlationGuid,
          );
          break;
        case "SettingsResponseMessage":
          this.settingCache.handleUpdateResponse(msg);
          break;
        case "CachingResultMessage":
          this.responseCacheResultExchanger.handleReceivedMessage(msg);
          break;
        default:
          this.logger.write(
            LogLevel.ERROR,
            `Unhandleable message received by inference worker process: ${JSON.stringify(msg)}`
          );
          break;
      }
    } catch (e) {
      this.logger.write(
        LogLevel.ERROR,
        `Error while handling message: ${e} (message: ${JSON.stringify(msg)})`
      );
    }
  }
}

/**
 * Entry point for the inference worker process. Sets up listeners and returns.
 */
async function main() {
  // Inside a worker process
  if (process === null || process === undefined || process.send === null || process.send === undefined) {
    // this shouldn't happen...
    throw new Error("No parent process available");
  }
  const logger : WorkerLoggingService = new WorkerLoggingService(m => process.send?.(m));
  const startupMsg = `Worker with pid ${process.pid} has started`
  console.log(startupMsg)
  logger.write(LogLevel.INFO, startupMsg);
  const telemetry : WorkerTelemetryService = new WorkerTelemetryService(m => process.send?.(m));
  try {
    // Register uncaught exception + unhandled rejection handlers so we can log & send telemetry
    process.on("uncaughtException", (e: Error, s) => {
      if (e instanceof Error) {
        logger.write(LogLevel.ERROR, `Uncaught exception error: ${e.message}`);
        if (e.stack) {
          logger.write(LogLevel.ERROR, `Unhandled exception error stack: ${e.stack}`);
        }
        telemetry.sendTelemetryEvent(TelemetryEventNames.WORKER_UNHANDLED_FAULT, {
          [TelemetryPropertyNames.EVENT_MESSAGE]: e.message,
          [TelemetryPropertyNames.EVENT_EXCEPTION_STACK]: e.stack ?? "null",
          [TelemetryPropertyNames.FAULT_TYPE]: "uncaughtException"
        });
      }
      else {
        // this code path shouldn't be reached if everything is properly throwing Errors, but handle this just in case.
        logger.write(LogLevel.ERROR, `Uncaught exception: ${e}`);

        telemetry.sendTelemetryEvent(TelemetryEventNames.WORKER_UNHANDLED_FAULT, {
          [TelemetryPropertyNames.EVENT_MESSAGE]: JSON.stringify(e),
          [TelemetryPropertyNames.EVENT_EXCEPTION_STACK]: "none",
          [TelemetryPropertyNames.FAULT_TYPE]: "uncaughtException-incorrectErrorTypeThrown"
        });
      }

    });
    process.on("unhandledRejection", (e: Error, s) => {
      if (e instanceof Error) {
        logger.write(LogLevel.ERROR, `Unhandled rejection error: ${e.message}`);
        if (e.stack) {
          logger.write(LogLevel.ERROR, `Unhandled rejection error stack: ${e.stack}`);
        }

        telemetry.sendTelemetryEvent(TelemetryEventNames.WORKER_UNHANDLED_FAULT, {
          [TelemetryPropertyNames.EVENT_MESSAGE]: e.message,
          [TelemetryPropertyNames.EVENT_EXCEPTION_STACK]: e.stack ?? "null",
          [TelemetryPropertyNames.FAULT_TYPE]: "unhandledRejection"
        });
      }
      else {
        // this code path shouldn't be reached if everything is properly throwing Errors, but handle this just in case.
        logger.write(LogLevel.ERROR, `Unhandled rejection exception: ${e}`);

        telemetry.sendTelemetryEvent(TelemetryEventNames.WORKER_UNHANDLED_FAULT, {
          [TelemetryPropertyNames.EVENT_MESSAGE]: JSON.stringify(e),
          [TelemetryPropertyNames.EVENT_EXCEPTION_STACK]: "none",
          [TelemetryPropertyNames.FAULT_TYPE]: "unhandledRejection-incorrectErrorTypeThrown"
        });
      }

    });
    const settingCache : WorkerSettingsStorage = new WorkerSettingsStorage(m => process.send?.(m));
    const lineCompletionService =
      await createLineCompletionServiceWithRetriesAsync(
        new Settings(settingCache),
        telemetry,
        logger
      );

    new InferenceWorker(
      logger,
      lineCompletionService,
      settingCache
    );
  } catch (e) {
    if (e instanceof OnnxRuntimeLoadFailureError){
      telemetry.sendTelemetryEvent(TelemetryEventNames.WORKER_INIT_UNSUPPORTED_RUNTIME, {
        [TelemetryPropertyNames.EVENT_MESSAGE]: e.message,
        [TelemetryPropertyNames.OS_ARCH]: process.arch,
        [TelemetryPropertyNames.EVENT_EXCEPTION_STACK]: e.stack ?? "null",
        [TelemetryPropertyNames.FAULT_TYPE]: "unsupportedOnnxRuntime"
      });
      logger.write(LogLevel.FATAL, `Unsupported arch for onnx runtime: ${e}`);

      // It seems like the easiest way to signal a situation to the parent
      // process is by using the exit code.
      // We use a reserved code to signal that onnx runtime cannot load in the current
      // architecture so that we don't try to re-create the process.
      process.exit(WorkerErrorCode.UNSUPPORTED_ONNX_RUNTIME_ARCH);
    }
    else {
      logger.write(LogLevel.FATAL, `Error initializing worker process: ${e}`);
      if (e instanceof Error) {
        telemetry.sendTelemetryEvent(TelemetryEventNames.WORKER_INIT_CRASH, {
          [TelemetryPropertyNames.EVENT_MESSAGE]: e.message,
          [TelemetryPropertyNames.EVENT_EXCEPTION_STACK]: e.stack ?? "null",
          [TelemetryPropertyNames.FAULT_TYPE]: "initCrash"
        });
      }
    }
  }
}

export { main };
